<template>
  <div class="container">
    <div class="score">{{score}}</div>
    <ul class="history">
      <li v-for="(item, index) in history" :key="index">
        <label>{{item.scoreType=='add'?'加':'减'}}</label>
        <label>{{item.scoreValue+'分'}}</label>
        <label>{{item.scoreContent}}</label>
        <label>{{item.scoreTime}}</label>
      </li>
    </ul>
    <button
      type="button"
      data-toggle="modal"
      data-target="#score"
      class="btn btn-primary remove-score"
      @click="changeScoreType('remove')"
    >{{scoreContents.remove.text}}</button>
    <button
      type="button"
      data-toggle="modal"
      data-target="#score"
      class="btn btn-primary add-score"
      @click="changeScoreType('add')"
    >{{scoreContents.add.text}}</button>

    <!-- Modal -->
    <div class="modal fade" id="score" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h4 class="modal-title" id="myModalLabel">{{scoreContents[scoreType].text}}</h4>
          </div>
          <div class="modal-body">
            <ul class="list-group">
              <li
                class="list-group-item"
                v-for="(item,index) in scoreContents[scoreType].contents"
                :key="index"
              >
                <label>
                  <input type="radio" name="scoreType" :value="item" v-model="scoreContent" />
                  {{item}}
                </label>
              </li>
              <li class="list-group-item">
                <select v-model="scoreValue">
                  <option value="1">1分</option>
                  <option value="2">2分</option>
                  <option value="3">3分</option>
                  <option value="4">4分</option>
                  <option value="5">5分</option>
                </select>
              </li>
            </ul>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-default" data-dismiss="modal">关闭</button>
            <button type="button" class="btn btn-primary" data-dismiss="modal" @click="save">保存</button>
          </div>
        </div>
      </div>
    </div>

    <object id="fileObj" classid="clsid:10946843-7507-44FE-ACE8-2B3483D179B7" width="0" height="0"></object>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      score: 0,
      scoreContents: {
        add: {
          text: "加分",
          contents: ["工作", "学习", "锻炼", "其他"]
        },
        remove: {
          text: "减分",
          contents: ["游戏", "休息", "其他"]
        }
      },
      scoreType: "add",
      scoreValue: 1,
      scoreContent: "其他",
      history: []
    };
  },
  mounted() {
    //部署路径，服务器特殊
    // this.$http.get("../../../personalityscore/static/score.json").then(res => {
    // this.$http.get("../../../static/score.json").then(res => {
    //   this.score = res.data.score;
    //   this.history = res.data.history;
    // });

    this.$http
      .get("https://personalityscore.firebaseio.com/score.json")
      .then(res => {
        this.score = res.data.score;
        this.history = res.data.history;
      });
  },
  methods: {
    changeScoreType: function(type) {
      this.scoreType = type;
    },
    save: function() {
      this.score += parseInt(this.scoreValue);
    }
  }
};
</script>

<style>
.remove-score {
  position: absolute;
  left: 10px;
  bottom: 10px;
}
.add-score {
  position: absolute;
  right: 10px;
  bottom: 10px;
}
.score {
  color: brown;
  font-size: 60px;
}
.history {
  list-style-type: none;
  padding: 0px 50px;
  margin-top: 20px;
}
.history li > label:last-of-type {
  margin-right: 0;
}
.history li > label:first-of-type {
  margin-right: 0;
}

.history li > label {
  margin-right: 20px;
}
</style>


